import React, { useState, useEffect, useContext } from "react";
import HashLoader from "react-spinners/HashLoader";
import Navbar from "./components/Navbar/Navbar";
import Intro from "./components/Intro/Intro";
import Services from "./components/Services/Services";
import "./App.css";
import Experience from "./components/Experience/Experience";
import Works from "./components/Works/Works";
import Portfolio from "./components/Portfolio/Portfolio";
import Contact from "./components/Contact/Contact";
import Footer from "./components/Footer/Footer";
import { themeContext } from "./Context";
import MyComponent from "./components/info/info";
import TypingEffect from './components/typing/Typingeffect';

function App() {
  const [loading, setLoading] = useState(true);
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  useEffect(() => {
    // Simulate a loading delay (e.g., fetching data or waiting for assets to load)
    setTimeout(() => setLoading(false), 3000); // You can adjust the delay as needed
  }, []);

  return (
    <div
      className="App"
      style={{
        background: darkMode ? "black" : "",
        color: darkMode ? "white" : "",
      }}
    >
      {loading ? (
        <div className="loader-container">
          <HashLoader color="#36d7b7" />
        </div>
      ) : (
        <>
          <TypingEffect />
          <Navbar />
          <Intro />
          <Services />
          <Experience />
          <Works />
          <Portfolio />
          <Contact />
          <MyComponent />
          <Footer />
        </>
      )}
    </div>
  );
}
console.clear();

export default App;
